import initMirageServer from '@Green-Dot-Corporation/eureka-lib-mock-utils/src/initMirageServer';
import registerBoardRoutes from './registerBoardRoutes';
import registerI18NRoutes from './registerI18NRoutes';

export default function registerRoutes() {
    const server = initMirageServer({
        routes() {
            registerBoardRoutes(this);
            registerI18NRoutes(this);
        },
    });

    return server;
}
