// image key naming convention = PARENT_COMPONENT_[LOCATION]_[PURPOSE]
// PARENT_COMPONENT = can be a nested path, ex EMAIL_ENTRY_BANNER
// LOCATION (relative to the parent component)= HEADING, BODY, FOOTING
// PURPOSE = SUCCESS, WARNING, INFO, TOOLTIP, ICON
import WARNING from './warning.png';

const MAPS = {
    WARNING,
};

export const IMAGE_KEYS = {};
Object.keys(MAPS)
    .sort()
    .forEach((k) => {
        IMAGE_KEYS[k] = k;
    });

const imageLibrary = MAPS;
export default imageLibrary;
