// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/effects.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import './button.scss';

.eureka-menu-button, %eureka-menu-button {
    display: inline-block;
    position: relative;

    &__button {
        @extend %eureka-button;
    }

    &__menu {
        @include eureka-box-shadow-elevated;
        background-color: var(--eureka-core-color-canvas, eureka-light-theme('core-color-canvas'));
        border: 1px solid var(--eureka-core-color-universal-decorative-border, eureka-light-theme('core-color-universal-decorative-border'));
        border-radius: var(--eureka-form-menu-border-radius, eureka-light-theme('form-menu-border-radius'));
        position: absolute;
        top: calc(100% + 3px);
        z-index: 1000;
    }

    &__menu-list {
        @include eureka-list-reset;
    }

    &__menu-list-option {
        @include eureka-flex-column;
    }

    &__menu-list-option:first-child .eureka-menu-button-option {
        border-radius: calc(var(--eureka-form-menu-border-radius, eureka-light-theme('form-menu-border-radius')) - 1px) calc(var(--eureka-form-menu-border-radius, eureka-light-theme('form-menu-border-radius')) - 1px) 0 0;
    }

    &__menu-list-option:last-child .eureka-menu-button-option {
        border-radius: 0 0 calc(var(--eureka-form-menu-border-radius, eureka-light-theme('form-menu-border-radius')) - 1px) calc(var(--eureka-form-menu-border-radius, eureka-light-theme('form-menu-border-radius')) - 1px);
    }
}
