import React from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { ContextIcon } from '@Green-Dot-Corporation/eureka-ui-icons';

// Styles
import './styles/avatar.scss';

const Avatar = (props) => {
    const { contextCls, text, imgURL, renderIcon, ...rest } = props;
    const shouldRenderImg = !!imgURL;
    const style = {
        ...(shouldRenderImg && { backgroundImage: `url('${imgURL}')` }),
    };
    const baseCls = 'eureka-avatar';
    const modCls = {
        [`${baseCls}--icon`]: !!renderIcon && !shouldRenderImg,
        [`${baseCls}--img`]: shouldRenderImg,
        [`${baseCls}--text`]: !!text && !shouldRenderImg && !renderIcon,
    };

    return (
        <span
            {...rest}
            className={cx(baseCls, modCls, contextCls)}
            style={style}
        >
            {!shouldRenderImg && (
                <span className={`${baseCls}__inner`}>
                    {renderIcon ? (
                        <ContextIcon renderIcon={renderIcon} />
                    ) : (
                        text
                    )}
                </span>
            )}
        </span>
    );
};

Avatar.propTypes = {
    contextCls: PropTypes.string,
    imgURL: PropTypes.string,
    text: PropTypes.string,
    renderIcon: PropTypes.func,
};

export default Avatar;
