import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from '@Green-Dot-Corporation/eureka-lib-router-utils';
import { ListItem } from '@Green-Dot-Corporation/eureka-ui-lists';
import { Avatar } from '@Green-Dot-Corporation/eureka-ui-avatars';

import { Button, MenuButton } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { Badge } from '@Green-Dot-Corporation/eureka-ui-badges';
import { EurekaCog, EurekaChat } from '@Green-Dot-Corporation/eureka-ui-icons';
import './styles/kanban-task.scss';

class KanbanTask extends PureComponent {
    static propTypes = {
        task: PropTypes.object,
        navigateWithinParent: PropTypes.func,
    };

    render() {
        const { task } = this.props;
        const { text, user } = task;
        const { avatar } = user ?? {};

        return (
            <ListItem contextCls={this.baseCls}>
                <span className={`${this.baseCls}__task-row1`}>
                    <span className={`${this.baseCls}__task-text`}>{text}</span>{' '}
                    <Avatar imgURL={avatar} />
                </span>
                <span className={`${this.baseCls}__task-row2`}>
                    <span className={`${this.baseCls}__task_tags`}>
                        <Badge>test</Badge>
                    </span>
                    <span className={`${this.baseCls}__task_actions`}>
                        <Button
                            renderIcon={this.renderCommentsIcon}
                            onClick={this.handleCommentsClick}
                        />
                        <MenuButton
                            renderIcon={this.renderIcon}
                            menuOptions={[
                                {
                                    text: 'Edit',
                                    value: 'EDIT_BUTTON',
                                },
                                {
                                    text: 'Delete',
                                    isDestructive: true,
                                    value: 'DELETE_BUTTON',
                                },
                            ]}
                        />
                    </span>
                </span>
            </ListItem>
        );
    }

    renderIcon = () => {
        return <EurekaCog />;
    };

    renderCommentsIcon = () => {
        return <EurekaChat />;
    };

    handleCommentsClick = () => {
        const { navigateWithinParent } = this.props;
        navigateWithinParent('/comments');
    };

    baseCls = 'kanban-task';
}

export default withRouter(KanbanTask);
