import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { IllustrationCallout } from '@Green-Dot-Corporation/eureka-ui-callouts';
import './styles/error-callout.scss';

class ErrorBoundaryCallout extends PureComponent {
    static defaultProps = {};

    static propTypes = {
        contextCls: PropTypes.string,
    };

    render() {
        const { contextCls, ...rest } = this.props;
        const calloutConfig = {
            ...rest,
        };

        return (
            <IllustrationCallout
                contextCls={cx(this.baseCls, contextCls)}
                {...calloutConfig}
            />
        );
    }

    baseCls = 'eureka-error-boundary-callout';
}

export default ErrorBoundaryCallout;
