import { get } from '@Green-Dot-Corporation/eureka-lib-fetch-utils';

export const getBoardConfig = async () => {
    const board = await get('/api/v1/board');

    const { cols, tasks } = board;
    const columnsByStatus = cols.reduce((acc, c) => {
        acc[c.status] = {
            col: c,
            tasks: [],
        };

        return acc;
    }, {});

    tasks.forEach((t) => {
        const { status } = t;
        columnsByStatus[status].tasks.push(t);
    });

    console.log(columnsByStatus);

    return columnsByStatus;
};

export default {};
