// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/effects.scss';
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';

.eureka-tile, %eureka-tile {
    @include eureka-box-shadow-subtle;
    background-color: var(--eureka-core-color-canvas, eureka-light-theme('core-color-canvas'));
    border-radius: var(--eureka-tile-border-radius, eureka-light-theme('tile-border-radius'));

    &__heading-container {
        border-bottom: var(--eureka-tile-heading-border-width, eureka-light-theme('tile-heading-border-width')) solid var(--eureka-core-color-universal-decorative-border, eureka-light-theme('core-color-universal-decorative-border'));
    }

    &__heading-container,
    &__content-container,
    &__content-container-inner {
        box-sizing: border-box;

        > :first-child {
            margin-top: 0;
        }

        > :last-child {
            margin-bottom: 0;
        }
    }

    &__heading {
        @include eureka-h5;
        font-weight: var(--eureka-tile-heading-font-weight, eureka-light-theme('tile-heading-font-weight'));
    }

    &__heading-container,
    &--pad-content &__content-container-inner {
        padding: #{eureka-to-rem(eureka-light-theme('core-system-scale') * 3)} #{eureka-to-rem(eureka-light-theme('core-system-scale') * 4)};
    }
}
