import React from 'react';
import initMirageServer from '@Green-Dot-Corporation/eureka-lib-mock-utils/src/initMirageServer';
import { BrowserRouter } from 'react-router-dom';
import ReactDOM from 'react-dom/client';
import reportWebVitals from './reportWebVitals';
import defaultDesignToken from './mock/branding/web.json';
import registerRoutes from './mock/registerRoutes';
import KanbanBoardModule from './KanbanBoardModule';
import importDefaultDesignToken from './mock/importDefaultDesignToken';
import appConfig from './mock/appConfigs/config.json';

import './styles/index.scss';

if (process.env.NODE_ENV === 'development') {
    // Shutdown any existing instance of server to avoid multiple pretender
    // instances error
    if (window.server && typeof window.server.shutdown === 'function') {
        window.server.shutdown();
    }

    // Create server and pass routes and other options
    window.server = initMirageServer({
        routes() {
            registerRoutes(this);
        },
    });
}

importDefaultDesignToken(defaultDesignToken);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <React.StrictMode>
        <BrowserRouter>
            <KanbanBoardModule
                appConfig={appConfig}
                feature={appConfig.featureConfig.kanbanboard}
            />
        </BrowserRouter>
    </React.StrictMode>,
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
