import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Tile } from '@Green-Dot-Corporation/eureka-ui-layouts';
import { List } from '@Green-Dot-Corporation/eureka-ui-lists';
import KanbanTask from './KanbanTask';
import './styles/kanban-board.scss';

export default class KanbanBoard extends PureComponent {
    static propTypes = {
        boardConfig: PropTypes.object,
        contextCls: PropTypes.string,
    };

    render() {
        const { boardConfig, contextCls } = this.props;

        return Object.getOwnPropertyNames(boardConfig).map((status) => {
            const item = boardConfig[status];
            const { col, tasks } = item;
            const { name, title } = col;

            return (
                <Tile
                    key={name}
                    headingLvl={3}
                    headingText={title}
                    contextCls={cx(`${this.baseCls}__column`, contextCls)}
                >
                    <List data={tasks} renderListItem={this.renderListItem} />
                </Tile>
            );
        });
    }

    renderListItem = (taskItem) => {
        return <KanbanTask key={taskItem.id} task={taskItem} />;
    };

    baseCls = 'kanban-module';
}
