import { Component } from 'react';
import PropTypes from 'prop-types';
import { VARIANTS as BUTTON_VARIANTS } from '@Green-Dot-Corporation/eureka-ui-buttons';
import { Callout, VARIANTS } from '@Green-Dot-Corporation/eureka-ui-callouts';
import { withRouter } from '@Green-Dot-Corporation/eureka-lib-router-utils';

import { registerPageErrorCallback } from './errorUtil';
import ErrorModal from './ErrorModal';

import ErrorBoundaryCallout from './ErrorBoundaryCallout';

class EurekaErrorBoundary extends Component {
    static getDerivedStateFromError() {
        return { hasError: true };
    }

    static propTypes = {
        calloutConfig: PropTypes.shape({
            title: PropTypes.string,
            msg: PropTypes.string,
            buttonText: PropTypes.string,
            contextCls: PropTypes.string,
            illustrationSrc: PropTypes.string,
            isPageHeading: PropTypes.bool,
        }),
        modalConfig: PropTypes.shape({
            dialogContextCls: PropTypes.string,
        }),
        navigateWithinParent: PropTypes.func,
        redirectURL: PropTypes.string,
        renderError: PropTypes.func,
        shouldReloadPageAfterError: PropTypes.bool,
        onBeforeNavigate: PropTypes.func,
    };

    constructor(e) {
        super(e);
        registerPageErrorCallback(this.pageErrorHandler);
    }

    state = {};

    componentDidCatch(error) {
        this.setState({ hasError: true, error });
    }

    render() {
        const { children, renderError } = this.props;
        const { hasError, error = {} } = this.state;

        if (hasError) {
            const errorRenderer = renderError ?? this.renderError;

            return errorRenderer(children, error);
        }

        return children;
    }

    renderError = () => {
        const { calloutConfig, modalConfig, children } = this.props;
        const { hasError, error = {} } = this.state;
        const { title, msg, buttonText, asModal } = error;
        const {
            title: defaultTitle,
            msg: defaultMsg,
            buttonText: defaultButtonText,
            contextCls: calloutContextCls,
            illustrationSrc,
            isPageHeading = true,
        } = calloutConfig ?? {};
        const { dialogContextCls } = modalConfig ?? {};

        const buttonLabel = buttonText ?? defaultButtonText;
        const titleText = title ?? defaultTitle;
        const msgText = msg ?? defaultMsg;

        if (hasError && !asModal) {
            const actions = buttonLabel && [
                {
                    text: buttonLabel,
                    variant: BUTTON_VARIANTS.PRIMARY,
                },
            ];

            return (
                <ErrorBoundaryCallout
                    contextCls={calloutContextCls}
                    headingLvl={1}
                    isPageHeading={isPageHeading}
                    title={titleText}
                    msg={msgText}
                    onActionClick={this.handleButtonClick}
                    actions={actions}
                    illustrationSrc={illustrationSrc}
                />
            );
        }

        const modalActions = [
            {
                variant: BUTTON_VARIANTS.PRIMARY,
                text: buttonLabel,
            },
        ];

        return (
            <>
                {children}
                <ErrorModal
                    dialogContextCls={dialogContextCls}
                    isOpen={hasError}
                    onActionClick={this.handleButtonClick}
                    shouldCloseOnEsc={false}
                    hasCloseButton={false}
                    shouldCloseOnOverlayClick={false}
                    modalActions={modalActions}
                >
                    <Callout
                        title={titleText}
                        msg={msgText}
                        variant={VARIANTS.ERROR}
                        shouldPadContent={false}
                    />
                </ErrorModal>
            </>
        );
    };

    handleButtonClick = () => {
        const {
            navigateWithinParent,
            redirectURL: defaultRedirectURL,
            shouldReloadPageAfterError: defaultShouldReloadPageAfterError,
            onBeforeNavigate,
        } = this.props;
        const { error = {} } = this.state;
        const { shouldReloadPageAfterError, redirectURL } = error;

        const finalURL = redirectURL ?? defaultRedirectURL;

        const shouldReload =
            shouldReloadPageAfterError ?? defaultShouldReloadPageAfterError;

        if (onBeforeNavigate) {
            onBeforeNavigate();
        }

        if (shouldReload) {
            window.location.href = finalURL;
        } else {
            navigateWithinParent(finalURL);
        }
        this.setState({ hasError: false, error: undefined });
    };

    pageErrorHandler = (error) => {
        this.setState({ hasError: true, error });
    };
}

export default withRouter(EurekaErrorBoundary);
