import {
    registerErrorHandler,
    unRegisterErrorHandler,
} from '@Green-Dot-Corporation/eureka-lib-fetch-utils';

let pageErrorCallback;
export const registerPageErrorCallback = (callback) => {
    pageErrorCallback = callback;
};

export const setPageError = (pageError) => {
    pageErrorCallback(pageError);
};

let fetchErrorHandler;
export const registerPageErrorInterceptor = (errorResolover) => {
    if (!fetchErrorHandler) {
        fetchErrorHandler = (e) => {
            const { error } = e;
            const pageError = errorResolover(error);

            if (pageError) {
                setPageError(pageError);
                throw pageError;
            }
        };

        registerErrorHandler(fetchErrorHandler);
    }
};

export const unregisterPageErrorInterceptor = () => {
    if (fetchErrorHandler) {
        unRegisterErrorHandler(fetchErrorHandler);
        fetchErrorHandler = undefined;
    }
};

export const raiseModalError = (options) => {
    pageErrorCallback({ ...options, asModal: true });
};

export const raisePageError = (options) => {
    pageErrorCallback({ ...options });
};

export default {};
