import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';
import { Modal } from '@Green-Dot-Corporation/eureka-ui-modals';

import './styles/error-boundary-modal.scss';

export default class ErrorModal extends PureComponent {
    static propTypes = {
        dialogContextCls: PropTypes.string,
    };

    render() {
        const { dialogContextCls, ...rest } = this.props;

        return (
            <Modal
                dialogContextCls={cx(this.baseCls, dialogContextCls)}
                {...rest}
            />
        );
    }

    baseCls = 'eureka-error-boundary-modal';
}
