import { PureComponent } from 'react';
import PropTypes from 'prop-types';
import {
    getTranslation,
    initTranslations,
} from '@Green-Dot-Corporation/eureka-lib-i18n-utils';
import { initializeImages } from '@Green-Dot-Corporation/eureka-ui-media';
import {
    LoadingFlow,
    EurekaErrorBoundary,
} from '@Green-Dot-Corporation/eureka-ui-loading';

// import initMirageServer from '@Green-Dot-Corporation/eureka-lib-mock-utils/src/initMirageServer';
import { Route, Routes } from 'react-router-dom';
import KanbanBoard from './components/KanbanBoard';
import registerRoutes from './mock/registerRoutes';
import Comments from './pages/Comments';
import Settings from './pages/Settings';
import { getBoardConfig } from './services/boardService';
import imageLibrary from './media/images/imageLibrary';
import './styles/board.scss';

if (
    process.env.NODE_ENV === 'development' ||
    process.env.NODE_ENV === 'production'
) {
    if (window.server && typeof window.server.shutdown === 'function') {
        registerRoutes(window.server);
    }
}

export default class KanbanBoardModule extends PureComponent {
    static propTypes = {
        appConfig: PropTypes.object,
        feature: PropTypes.object,
    };

    state = {
        isLoading: true,
        hasError: false,
    };

    render() {
        const { isLoading, hasError, boardConfig } = this.state;

        return (
            <EurekaErrorBoundary>
                <LoadingFlow
                    isLoading={isLoading}
                    hasError={hasError}
                    contextCls={`${this.baseCls}--loader`}
                    errorStateLocale={{
                        title: getTranslation('loading.error.title'),
                        msg: getTranslation('loading.error.msg'),
                    }}
                >
                    <div className={this.baseCls}>
                        <Routes>
                            <Route
                                path="/"
                                exact
                                element={
                                    <KanbanBoard boardConfig={boardConfig} />
                                }
                            />
                            <Route
                                path="/comments"
                                element={<Comments boardConfig={boardConfig} />}
                            />
                            <Route
                                path="/settings"
                                element={<Settings boardConfig={boardConfig} />}
                            />
                        </Routes>
                    </div>
                </LoadingFlow>
            </EurekaErrorBoundary>
        );
    }

    componentDidMount() {
        this.setState({ isLoading: true }, () => {
            this.initialize();
        });
    }

    initializeTranslations = async () => {
        const { appConfig } = this.props;
        const { resourcePath, localePath } = appConfig;

        await initTranslations({
            defaultTranslations: {
                'en-US': () => import('./i18n/en-US.json'),
            },
            localizationPath: `${resourcePath}${localePath}`,
        });
    };

    initializeImageLibrary = async () => {
        const { appConfig, feature = {} } = this.props;
        const { resourcePath, branding: { mediaPath } = {} } = appConfig;

        await initializeImages({
            defaultImages: imageLibrary,
            overridePath: `${resourcePath}${mediaPath}/${
                feature.id || 'kanbanboard'
            }`,
        });
    };

    initialize = async () => {
        const newState = {
            isLoading: false,
        };

        const promises = [
            this.initializeTranslations(),
            this.initializeImageLibrary(),
            getBoardConfig(),
        ];
        const results = await Promise.allSettled(promises);

        const rejectedIndex = results.findIndex((r) => {
            return r.status === 'rejected';
        });

        if (rejectedIndex !== -1) {
            newState.hasError = true;
            console.log(results[rejectedIndex]);
        } else {
            const [, , boardConfig] = results;

            newState.boardConfig = boardConfig.value;
        }

        this.setState(newState);
    };

    baseCls = 'kboard-container';
}
