// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/resets.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';
@import './button.scss';

.eureka-menu-button-option, %eureka-menu-button-option {
    @include eureka-button-reset;
    @include eureka-focus-state-halo;
    @include eureka-hover-state;
    @include eureka-disabled-state;
    color: inherit;
    box-sizing: border-box;
    border: 1px solid transparent;
    text-align: left;
    white-space: nowrap;
    $this: '.eureka-menu-button-option';

    @media (hover: hover) {
        &:enabled:hover {
            background-color: var(--eureka-core-color-universal-hover-bg, eureka-light-theme('core-color-universal-hover-bg'));
        }
    }

    &__content-container {
        @extend %eureka-button__content-container;
        display: block;
    }

    &__text-container {
        @extend %eureka-button__text-container;
    }

    &__icon-container {
        @extend %eureka-button__icon-container;
    }

    &__icon-container + &__text-container {
        vertical-align: middle;
    }

    &__icon-safe-space {
        @extend %eureka-button__icon-safe-space;
    }

    &__icon {
        @extend %eureka-button__icon;
        font-size: 100%;
    }

    &--is-destructive {
        color: var(--eureka-btn-secondary-destructive-color-text, eureka-light-theme('btn-secondary-destructive-color-text'));

        @media (hover: hover) {
            &:enabled:hover {
                color: var(--eureka-btn-secondary-destructive-hover-color-text, eureka-light-theme('btn-secondary-destructive-hover-color-text'));
            }
        }
    }
}
