// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/mixins/layout.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-avatar, %eureka-avatar {
    @include eureka-flex-row;
    width: 1em;
    height: 1em;
    color: var(--eureka-context-icon-primary-color, eureka-light-theme('context-icon-primary-color'));
    background-color: var(--eureka-avatar-color-bg, eureka-light-theme('avatar-color-bg'));
    font-size: 50px;
    text-align: center;
    box-sizing: border-box;
    border-radius: 100%;

    &__inner {
        // width: 1em;
        height: 1em;
        font-size: calc(1em * 0.44);
        line-height: 1;
        user-select: none;
        display: block;
        margin: auto;
    }

    .eureka-context-icon {
        color: inherit;

        &__icon {
            color: inherit;
            font-size: inherit;
        }
    }

    &--img {
        background-position: center;
        background-size: cover;
    }
}
