// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-badge, %eureka-badge {
    color: var(--eureka-badge-color-text, eureka-light-theme('badge-color-text'));
    background-color: var(--eureka-badge-color-bg, eureka-light-theme('badge-color-bg'));
    font-size: var(--eureka-core-typography-footnote-font-size, eureka-light-theme('core-typography-footnote-font-size'));
    line-height: var(--eureka-core-typography-line-height, eureka-light-theme('core-typography-line-height'));
    box-sizing: border-box;
    border-radius: var(--eureka-badge-border-radius, eureka-light-theme('badge-border-radius'));
    display: inline-block;
    padding: #{eureka-to-rem(eureka-light-theme('core-system-scale'))} #{eureka-to-rem(eureka-light-theme('core-system-scale') * 2)};

    &--affirmative {
        color: var(--eureka-badge-affirmative-color-text, eureka-light-theme('badge-affirmative-color-text'));
        background-color: var(--eureka-badge-affirmative-color-bg, eureka-light-theme('badge-affirmative-color-bg'));
    }

    &--danger {
        color: var(--eureka-badge-error-color-text, eureka-light-theme('badge-error-color-text'));
        background-color: var(--eureka-badge-error-color-bg, eureka-light-theme('badge-error-color-bg'));
    }
}
